import React,{Component} from 'react';
import { Card, CardImg, CardText, CardBody,
    CardTitle, Breadcrumb, BreadcrumbItem,Button,Modal, ModalHeader, ModalBody,Label,Row,Col} from 'reactstrap';
import { Link } from 'react-router-dom';
import {Control,LocalForm,Errors} from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);



class CommentForm extends Component{

    constructor(props){
        super(props);

        this.state = {
             isModalOpen: false
        };

this.toggleModal = this.toggleModal.bind(this);

    }
toggleModal(){
    this.setState({isModalOpen:!this.state.isModalOpen})
}

handleSubmit(values) {
       
         this.props.postComment(this.props.dishId, values.rating , values.yourname ,  values.textarea );
       
        
    }


render(){
    

    return (<>

        <Button onClick={this.toggleModal} className="mt-3 btn-outline-secondary"> <i className="fa fa-pencil"></i>&nbsp;&nbsp; Submit Comment </Button>


<Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit comment</ModalHeader>
                    <ModalBody>
                    <LocalForm onSubmit={(values) => this.handleSubmit(values)}>


                   
                 <Row className="form-group">
                             <Label htmlFor="rating" md={2}>Rating</Label>
                            <Col md="12"> 
                                <Control.select model=".rating" id="rating" name="rating" className="form-control">
                                <option>1</option>
                                <option>2</option>
                                <option>3</option>
                                <option>4</option>
                                <option>5</option>
                                </Control.select>

                             </Col>
                        </Row>
                        <Row className="form-group">
                             <Label htmlFor="yourname" md={3}>Your Name</Label>
                                 <Col md="12"> 
                                <Control.text model=".yourname" id="yourname" name="yourname" className="form-control"
                                     validators={{
                                            required, minLength: minLength(3), maxLength: maxLength(15)
                                        }}
                                />
                                <Errors
                                        className="text-danger"
                                        model=".yourname"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 characters',
                                            maxLength: 'Must be 15 characters or less'
                                        }} />
                                 </Col>

                        </Row>
                        <Row className="form-group">
                             <Label htmlFor="textarea" md={3}>Comment</Label>
                                 <Col md="12"> 
                                <Control.textarea model=".textarea" id="textarea" name="textarea" rows="4" className="form-control"/>
                                 </Col>
                        </Row>
                         <Row className="form-group">
                         <Col md={10}>
                                 <Button color="primary"> Submit </Button>
                                 </Col>
                         </Row>

                         </LocalForm>
                    </ModalBody>
                </Modal>

                </>

        );
}

}


 function RenderDish({dish}) 
 {
 	if (dish != null)
    	{
            return(<FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                <Card>
                    <CardImg top src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                      <CardTitle>{dish.name}</CardTitle>
                      <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
                </FadeTransform>
            );
           }
        else{
            return(
                <div></div>
            );
    
}


    }

   function RenderComments({comments, postComment, dishId}) {

		if(comments != null){
             

			const Comments=comments.map((com)=> {return(
                  <Stagger in>
                  <Fade in>

			 <li key={com.id} > <div className='mt-4'>{com.comment} </div>
			 <div className='mt-4'>-- {com.author},{new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(com.date)))} </div>
			 </li>

  </Fade>
         </Stagger>    
				);
}
		);


			return(
			<div className="col-12 col-md-5 m-1">
					<h4>Comments</h4>
			<ul> {Comments} </ul>
             <CommentForm dishId={dishId} postComment={postComment} />

            
			</div>
			
			);
}
		else{
			return(
                <div></div>
            );
}
}


    function Dishdetail(props)
    {
        if (props.isLoading) {
            return(
                <div className="container">
                    <div className="row">            
                        <Loading />
                    </div>
                </div>
            );
        }
        else if (props.errMess) {
            return(
                <div className="container">
                    <div className="row">            
                        <h4>{props.errMess}</h4>
                    </div>
                </div>
            );
        }
        else if (props.dish != null) 
        
        {
    	return(

    <div className="container">
     <div className="row">
                    <Breadcrumb>

                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>                
                </div>
    
    <div  className="row">
   
<div className="col-12 col-md-5 m-1">
   <RenderDish dish={props.dish}
    />
</div>


    <RenderComments comments={props.comments}
        postComment={props.postComment}
        dishId={props.dish.id}
      />

</div>
</div>
 	

    	);
    }
    }

export default Dishdetail;